@import "https://maxcdn.bootstrapcdn.com/font-awesome/4.4.0/css/font-awesome.min.css";
*, :before, :after {
  box-sizing: border-box;
}

body {
  background: #eee;
  margin: 0;
  padding: 0;
  font-family: Roboto, sans-serif;
  position: relative;
}

.todo-list .header {
  width: 100%;
  height: 80px;
  z-index: 5;
  background: #a525b9;
  border-radius: 0 0 10px 10px;
  padding: 1em;
  position: fixed;
  top: 50px;
  left: 0;
  box-shadow: 0 2px 4px #3232324d;
}

.todo-list .header .header__input {
  width: 100%;
  height: 100%;
  color: #fff;
  background: #fff3;
  border: none;
  border-radius: 5px 25px 25px 5px;
  outline: none;
  padding: .7em 1.2em;
  font-size: 1.2rem;
}

.todo-list .header .header__button {
  width: 50px;
  height: 50px;
  z-index: 2;
  text-align: center;
  color: #a525b9;
  cursor: pointer;
  background: #fff;
  border: none;
  border-radius: 25px;
  font-size: 1.3rem;
  position: absolute;
  top: 15px;
  right: 15px;
}

.todo-list main {
  background: #eee;
  margin-top: 78px;
}

.todo-list hr {
  width: 50%;
  color: #32323233;
}

.todo-list .list {
  margin: 0;
  padding: 1em;
}

.todo-list .list .list__item {
  width: 100%;
  background: #fff;
  border-radius: 3px;
  justify-content: space-between;
  margin: 1em 0;
  padding: 1em;
  font-size: 1.3em;
  list-style: none;
  display: flex;
  box-shadow: 0 1px 2px #32323233;
}

.todo-list .list .list__buttons {
  font-size: 1.3rem;
}

.todo-list .list .list__button {
  text-align: center;
  width: 50px;
  cursor: pointer;
  background: #fff;
  border: none;
  font-size: 1.4rem;
}

.todo-list .list .fa-trash-o {
  color: #32323280;
}

.todo-list .list .list__bar {
  color: #3232324d;
  font-size: 1.5rem;
}

.todo-list .list.current-list .fa-check-circle-o {
  color: #25b99a;
}

.todo-list .list.done-list .list__item {
  opacity: .6;
}

.todo-list .list.done-list .list__item .list__item-name {
  text-decoration: line-through;
}

.todo-list .list.done-list .fa-check-circle {
  color: #25b99a;
}

